.App {
  text-align: center;
  width: 100vw;
  height: 100vh;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 40px;
  height: 100px;
  padding: 20px;
}

.circle-wrapper{
  min-width: 100%;
  min-height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background: url("../images/Diocircle.png") center;
  background-size: 150%;
  background-repeat: no-repeat;
  -webkit-filter: saturate(80%);
          filter: saturate(80%);
  opacity: 0.8;
}

.circle-body {
  position: relative;
  -webkit-transform-origin: center;
      -ms-transform-origin: center;
          transform-origin: center;
  -webkit-filter: saturate(100%);
          filter: saturate(100%);
  opacity: 1;
  -webkit-transition: 3s;
  -o-transition: 3s;
  transition: 3s;
}

.habbit-header {
  background-color: aquamarine;
  position: absolute;
  -webkit-transform-origin: 0 0;
      -ms-transform-origin: 0 0;
          transform-origin: 0 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-transition: 3s;
  -o-transition: 3s;
  transition: 3s;
  cursor: pointer;
  color: aliceblue;
}

.decision-header {
  background-color: aquamarine;
  position: absolute;
  -webkit-transform-origin: 0 0;
      -ms-transform-origin: 0 0;
          transform-origin: 0 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-transition: 3s;
  -o-transition: 3s;
  transition: 3s;
  cursor: pointer;
  color: aliceblue;
}

.step {
  position: absolute;
  -webkit-transform-origin: 0 0;
      -ms-transform-origin: 0 0;
          transform-origin: 0 0;
  -webkit-transition: 3s;
  -o-transition: 3s;
  transition: 3s;
}

.decision {
  position: absolute;
  -webkit-transform-origin: 0 0;
      -ms-transform-origin: 0 0;
          transform-origin: 0 0;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
}

.decision:hover {
  font-size: 30px;
  color: blueviolet;
}

.rightBorder {
  height: 100%;
  position: absolute;
  -webkit-transform-origin: 100% 100%;
      -ms-transform-origin: 100% 100%;
          transform-origin: 100% 100%;
  border-bottom: solid 2px transparent;
  border-top:  solid 2px transparent;
  border-right:  solid 2px transparent;
}

.step-text {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
  position: absolute;
  z-index: 5;
  font-size: small;
  color: aliceblue;
}

.leftBorder {
  height: 100%;
  position: absolute;
  border-bottom: solid 2px transparent;
  border-top:  solid 2px transparent;
  border-left:  solid 2px transparent;
  -webkit-transform-origin: 0 100%;
      -ms-transform-origin: 0 100%;
          transform-origin: 0 100%;
}

.corner{
  position: absolute;
  width: 2px;
  height: 2px;
  background-color: crimson;
}

.center {
  position: absolute;
  width: 2px;
  height: 2px;
  background: url("../images/stairway.png") top 55px left -20px;
  background-size:contain;
  -webkit-transform-origin: center;
      -ms-transform-origin: center;
          transform-origin: center;
  -webkit-transition: 3s;
  -o-transition: 3s;
  transition: 3s;
}

.not-chosen {
  -webkit-filter: blur(2px) saturate(30%);
          filter: blur(2px) saturate(30%);
  opacity: 0.5;
}

.habbit-header.not-chosen {
  opacity: 0.9;
}


